* {
    margin: 0;
    padding: 0;
}

body {
    background: #a0a0a0;
    width: 100%;
}

.main-page-container {
    display: flex;
    justify-content: left;
}

.navigation-container {
    background: #2d2d2d;
    height: 4vh;
    padding-top: 5px;
}

.navigation-container a {
    font-size: 18pt;
    font-family: sans-serif;
    color: white;
    text-decoration: none;
    margin: 0 20px;
}

.navigation-container button {
    background: inherit;
    border: none;
    outline: none;
    font-size: 16pt;
    font-family: sans-serif;
    color: white;
    margin: 0 30px;
    transition: 1s;
}

.navigation-container button:hover {
    color: #ffd700;
}

.navigation-container a:last-child {
    margin-left: 30%;
    cursor: default;
}

.instruments-container {
    width: 6%;
    height: 75vh;
    padding-top: 20vh;
    background-color: #1d1d1d;
    display: flex;
    flex-direction: column;
}

.instruments-container button {
    width: 46px;
    height: 46px;
    margin: 5px;
    outline: none;
    background-size: 46px 46px;
}

.pen {
    background: url('./components/componentImages/pen.png') #3a3a3a;
}

.bucket {
    background: url('./components/componentImages/bucket.png') #3a3a3a;
}

.eraser {
    background: url('./components/componentImages/eraser.png') #3a3a3a;
}

.layers-container {
    height: 95vh;
    background: #1d1d1d;
    width: 110px;
    padding-left: 5px;
    padding-right: 10px;
    border-left: 2px solid #888888;
}

.canvas-container {
    height: 100px;
    width: 100px;
    border: 4px solid #2d2d2d;
    margin: 10px auto;
    background: url('./components/componentImages/canvasBackground.jpg');
    background-size: 100px 100px;
}

.canvas-container:hover {
    border: 2px solid #888888;
}

.button-container {
    position: absolute;
    width: 100px;
    height: 100px;
}

.layer-number {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 30px;
    height: 30px;
    text-align: center;
    font-size: 14pt;
    font-weight: bold;
    background: #ffd700;
}

.delete, .copy, .swap {
    width: 30px;
    height: 30px;
    border: none;
    position: relative;

}

.delete {
    left: 70%; 
    background: url('./components/componentImages/delete.png') no-repeat #888888;
    background-size: 20px 20px;
    background-position: center;
}

.copy {
    top: 70%;
    right: 30%;
    background: url('./components/componentImages/copyIcon.png') no-repeat #888888;
    background-size: 20px 20px;
    background-position: center;
}

.swap {
    left: 10%;
    top: 70%;
    background: url('./components/componentImages/swap.png') no-repeat #888888;
    background-size: 20px 20px;
    background-position: center;
}

.preview {
    background: url('./components/componentImages/canvasBackground.jpg');
}

.new-layer {
    width: 110px;
    height: 100px;
    border: 2px dotted #888888;
    background: url('./components/componentImages/newLayer.png') no-repeat;
    background-size: 110px 100px;
    margin-top: 20px;
}

.drawing-field {
    width: 52%;
    padding: 2% 100px 0;
}

.canvas-field-container {
    position: absolute;
}

.drawing-field canvas {
    background: url('./components/componentImages/canvasBackground.jpg');
}

.viewer-container {
    width: 330px;
    height: 95vh;
    background: #1d1d1d;
    justify-content: center;
}

.viewer {
    width: 300px;
    height: 300px;
    border: 2px solid #ffd700;
    background: #3a3a3a;
    margin: 20px auto;
}

.fps-counter {
    border: 2px solid #888888;
    width: 60px;
    margin-left: 20px;
    color: #ffd700;
    font-weight: bold;
    text-align: center
}

.range-slider {
    margin-left: 20px;
}

.not-active {
    border: 2px solid #2d2d2d;
}

.active {
    border: 2px solid #ffd700;
}

.active-canvas {
    display: block;
}

.not-active-canvas {
    display: none;
}