* {
  margin: 0;
  padding: 0;
}

.start-page {
  background: #222222;
}

.nav-bar {
  padding: 5px;
  background: #2d2d2d;
  border: 1px solid black;
}

.nav-bar a {
  color: white;
  text-decoration: none;
  font-size: 18pt;
  margin-right: 20px;
}

.nav-bar button {
  background: inherit;
  font-size: 14pt;
  color: white;
  border: none;
  outline: none;
  margin: 0 20px 0 20px;
  transition: 1s;
}

.nav-bar button:hover {
  color: #ffd700;
}

.nav-bar a:last-child {
  margin-left: 50%;
  color: white;
  font-size: 18pt;
}

.intro-container {
  background: #525252;
  width: 90%;
  display: flex;
  padding: 20px 0 20px 5px;
}

.title-text {
  color: white;
  font-size: 20pt;
  font-family: sans-serif;
  margin: 50px 20px 20px 10px;
}

.subtitle-text {
  color: #ccccb4;
  font-size: 20pt;
  font-family: sans-serif;
  margin: 50px 20px 20px 10px;
}

.subtitle-text a {
  color: white;
  text-decoration: none;
  outline: none;
  font-size: 22pt;
  transition: 1s;
}

.subtitle-text a:hover {
  color: #ffd700;
}

.intro button {
  background: #ffd700;
  color: #333333;
  font-size: 20pt;
  font-weight: bold;
  outline: none;
  border: none;
  width: 180px;
  height: 50px;
  margin: 10px 15px 0 5px;
}


.example-container {
  background: #212121;
  margin: 70px 0 0 10%;
  padding: 20px 40px;
}

.example-text {
  color: white;
  font-size: 18pt;
}

.example-images {
  display: inline-block;
  margin: 10px 5px 10px 5px;
  border: 1px solid gray;
}

.example-images p {
  color: white;
  font-size: 12pt;
  font-family: sans-serif;
  border: 2px solid gray;
  border-collapse: inherit;
  background: black;
}

.aboutus-container {
  background: #181818;
  padding: 30px 20%;
  display: grid;
  grid-template-columns: 30% 30%;
}

.aboutus-info {
  color: white;
  display: flex;
  margin: 10px 30px 10px 30px;
}

.aboutus-info h3 {
  font-size: 18pt;
}

.aboutus-info p {
  font-size: 14pt;
}

.footer-bar {
  color: white;
  width: 40%;
  margin: 0 auto;
}

.footer-bar span {
  display: inline-block;
  width: 100px;
  margin: 10px 50px 10px 50px;
}

.footer-bar a {
  color: #525252;
  text-decoration: none;
  transition: 1s;
}

.footer-bar a:hover {
  color: white;
  text-decoration: underline;
}

.login-container, .create-container {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

.login-window, .create-window {
  width: 600px;
  height: 300px;
  background: #2d2d2d;
  border: 2px solid #525252;
  border-radius: 15%;
  display: flex;
  flex-direction: column;
}

.login-window, .create-window p {
  margin-top: 50px;
  text-align: center;
  color: #ffd700;
  font-size: 18pt;
  font-family: sans-serif;
}

#name-container {
  width: 80%;
  height: 30px;
  border: none;
  outline: none;
  font-family: sans-serif;
  font-size: 16pt;
  font-weight: bold;
  color: #181818;
  background: #ccccb4;
  margin: 50px auto;
}

.submit-name {
  width: 150px;
  height: 70px;
  font-size: 16pt;
  font-weight: bold;
  background: #ffd700;
  border: none;
  outline: none;
  margin: 0 auto;
  transition: 1s;
}

.submit-name:hover {
  color: white;
}

.create-window button {
  width: 150px;
  height: 150px;
  margin: 10px auto;
  font-size: 18pt;
  font-family: sans-serif;
  font-weight: bold;
  background: #ffd700;
  border: none;
  outline: none;
}

.display-false {
  display: none;
}

.display-true-fixed {
  display: fixed;
}